.ec-minicart{display:inline}.ec-minicart__button{position:relative}.ec-minicart__button .badge{position:absolute;top:-.2rem;right:-.2rem;font-size:.8rem}.ec-minicart__sidebar{position:fixed;width:330px;max-width:100%;height:100%;top:0;right:0;z-index:1100;border-radius:0}.ec-minicart__sidebar>article{overflow-y:auto}.ec-minicart__summary{color:var(--gray);display:-webkit-box;display:flex;-webkit-box-align:center;align-items:center;-webkit-box-pack:justify;justify-content:space-between;padding-bottom:1rem}.ec-minicart__subtotal{color:var(--secondary)}